@use '../util' as *;

h1, h2, h3 {
    margin-top: 0px;
    line-height: 1.1;
}

h1{
    font-size: torem(24);
    margin-bottom: torem(3);
    @include breakpoint(large){
        font-size: torem(28);
    }
}

h2{
    font-size: torem(24);
    margin-bottom: torem(24);
}

a, a:visited, a:active {
    text-decoration: none;
}